import PropTypes from 'prop-types'

import DialogBox from '../Common/DialogBox'
import Modal from '../Common/Modal'

const styles = {
  description: {
    fontStyle: 'italic'
  }
}

function Settings (props) {
  const { settings, onUpdate, onClose } = props

  function handleUpdateSetting (key, value) {
    onUpdate({ ...settings, [key]: value })
  }

  return (
    <Modal onDismiss={onClose}>
      <DialogBox onDismiss={onClose}>
        <h1>Settings</h1>
        <h2>Experimental</h2>

        <h3>
          <label htmlFor="enable-devicetree-parsing">
            <input
              id="enable-devicetree-parsing"
              type="checkbox"
              checked={settings.devicetreeParsing || false}
              onChange={e => (
                handleUpdateSetting('devicetreeParsing', e.target.checked)
              )}
            />
            Enable Devicetree Parsing
          </label>
        </h3>
        <p style={styles.description}>
          Parse keymap from the <code>.keymap</code> file instad of
          <code>keymap.json</code>.
        </p>

        <h3>
          <label htmlFor="enable-auto-layout">
            <input
              id="enable-auto-layout"
              type="checkbox"
              checked={settings.attemptAutoLayout || false}
              onChange={e => (
                handleUpdateSetting('attemptAutoLayout', e.target.checked)
              )}
            />
            Enable Automatic Layout Parsing
          </label>
        </h3>
        <p style={styles.description}>
          Attempt to generate a layout based on the ZMK transformation matrix
          defined for the keyboard. This is a best-guess at putting keys in the
          right spot  just to get you up and running with the keymap editor. It
          won't try to guess at differently sized keys and row-staggered boards
          are not a priority.
        </p>
        <p style={styles.description}>
          If this <em>does</em> work you can commit the layout to your repository
          under <code>config/info.json</code> and customize from there.
        </p>
      </DialogBox>
    </Modal>
  )
}

Settings.propTypes = {
  settings: PropTypes.object.isRequired,
  onUpdate: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired
}

Settings.defaultProps = {
  settings: {}
}

export default Settings
