import { useContext } from 'react'

import styles from './styles.module.css'
import { SettingsContext } from '../providers'

function ComboEditorRequiresDevicetree () {
  const { openSettingsDialog } = useContext(SettingsContext)
  const settingsLink = (
    <span onClick={openSettingsDialog} className={styles.linkish}>
      Settings
    </span>
  )

  return (
    <div style={{ maxWidth: '500px', margin: '80px auto 0' }}>
      <h2> Experimental features required</h2>
      <p>Support for combos in the keymap editor is brand new!</p>
      <p>
        I'm glad you're interested in trying it out, but at this time you must
        enable support for making inline edits to your existing <code>.keymap</code>
        file which has been in the works for a little while now but I'm still
        considering it "experimental".
      </p>
      <p>
        If you want to give it a try, see the {settingsLink} dialog for more
        details.
      </p>
    </div>
  )
}

export default ComboEditorRequiresDevicetree
